.logo-child,
.logo-item {
  position: absolute;
  top: 15.84px;
  left: 15.61px;
  border: 2px solid var(--color-white);
  box-sizing: border-box;
  width: 22.39px;
  height: 23.41px;
  transform: rotate(-45deg);
  transform-origin: 0 0;
}
.logo-item {
  top: 26.05px;
  left: 15px;
}
.logo1 {
  position: relative;
  background-color: var(--color-white);
  width: 33px;
  height: 42.6px;
}
.ai-marketing {
  position: relative;
  line-height: 168.6%;
  font-weight: 800;
}
.logo {
  flex: 1;
  display: flex;
  flex-direction: row;
  padding: 0 15px;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-mini);
}
.services {
  position: relative;
  line-height: 44px;
}
.header,
.menu {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.menu {
  padding: 0 52px;
  justify-content: flex-end;
  gap: 35px;
  text-align: center;
  font-size: var(--font-size-lg);
  color: var(--color-slategray);
}
.header {
  align-self: stretch;
  height: 42.6px;
  justify-content: center;
}
.marketing-avec-lia {
  color: var(--color-mediumslateblue);
}
.titre {
  align-self: stretch;
  position: relative;
  font-weight: 800;
}
.agence-spcialise-dans {
  font-weight: 300;
}
.faire-grandir-votre {
  font-weight: 600;
  color: var(--color-white);
}
.sous-titre {
  align-self: stretch;
  position: relative;
  font-size: 24px;
  line-height: 44px;
  color: var(--color-slategray);
}
.boutoncontact,
.contactez-nous {
  align-self: stretch;
  display: flex;
  align-items: center;
  justify-content: center;
}
.contactez-nous {
  flex: 1;
  position: relative;
  font-size: var(--font-size-xl);
  line-height: 168.6%;
  font-family: var(--font-manrope);
  color: var(--color-white);
  text-align: center;
}
.boutoncontact {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: var(--color-mediumslateblue);
  border-radius: 15px;
  box-shadow: 0 5px 14px rgba(126, 128, 245, 0.4);
  height: 63px;
  flex-direction: column;
}
.button-frame,
.headingcontent,
.headingframe {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.button-frame {
  width: 299px;
  padding: 27px 3px;
  box-sizing: border-box;
}
.headingcontent,
.headingframe {
  align-self: stretch;
}
.headingcontent {
  padding: 0 75px;
  gap: 49px;
  text-align: center;
  font-size: 80px;
}
.headingframe {
  background-color: #000;
  padding: 24px 0;
  gap: 74px;
}
.frame-child,
.frame-item {
  align-self: stretch;
  position: relative;
  background-color: var(--color-mediumslateblue);
  height: 303px;
  overflow: hidden;
  flex-shrink: 0;
}
.frame-item {
  background-color: var(--color-slategray);
  height: 211px;
}
.headingframe-parent {
  position: relative;
  background-color: var(--color-white);
  width: 100%;
  height: 1316px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: left;
  font-size: var(--font-size-3xl);
  color: var(--color-white);
  font-family: var(--font-manrope);
}
@media screen and (max-width: 960px) {
  .ai-marketing {
    font-size: var(--font-size-xl);
  }
  .services {
    font-size: var(--font-size-mini);
  }
  .titre {
    font-size: 60px;
  }
  .sous-titre {
    font-size: var(--font-size-3xl);
    line-height: 40px;
  }
}
@media screen and (max-width: 420px) {
  .logo {
    flex-direction: row;
    gap: var(--gap-mini);
    align-items: center;
    justify-content: flex-start;
    flex: unset;
    align-self: stretch;
  }
  .menu {
    display: none;
  }
  .header {
    flex-direction: column;
    gap: 0;
    align-items: flex-start;
    justify-content: center;
  }
  .titre {
    font-size: 45px;
  }
  .sous-titre {
    line-height: 30px;
  }
  .contactez-nous,
  .sous-titre {
    font-size: var(--font-size-lg);
  }
  .boutoncontact {
    height: 55px;
  }
}
